@media only screen and (min-width: 5px) and (max-width: 340px) {
  .personalSkills{
    background-color: #fff;
    height: 78vh;
    width: 23.5rem;
    padding: 5%;
  }
  
  .lista {
    margin-top: 20%;
  }
  
  .textoLista {
    margin-top: 10%;
    font-family: 'Scope One', serif;
    font-size: 12px;
  }
  
  .focoSkills {
    width: 21%;
    margin-top: 5%;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
  }
  
}

